.content-multas {
    width: 100%;
    height: 100%;
}

.content-multas .content {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    height: 70vh;
}

.content-multas .content .title h1 {
    font-size: 1.2vw;
    font-weight: 500;
    margin-right: 10.5vw;
    margin-bottom: -.8vw;
}

.content-multas .content label {
    display: block;
    color: rgba(48, 48, 48, 0.987);
    font-family: "Poppins";
    font-size: .9vw;
    margin-left: .5vw;
    margin-bottom: .5vw;
}

.content-multas .content input {
    background: rgba(85, 85, 85, 0.159);
    border: 0;
    border-radius: 3vw;
    color: #131313;
    padding: 1vw;
    width: 15vw;
    max-width: 15vw;
    height: 3vw;
    padding-left: 1vw;
}

.content-multas .content input:focus {
    outline: none;
}

.content-multas .content .passaporte {
    margin-top: .8vw;
}

.content-multas .content .quantia {
    margin-top: 1vw;
}

.content-multas .content .button button {
    padding: .7vw 6vw;
    max-width: 15vw;
    border: 0;
    background: rgb(34, 33, 33);
    color: #f1f1f1;
    border-radius: 2vw;
    height: 3vw;
    font-family: "Poppins";
    display: block;
    margin-top: 1vw;
    cursor: pointer;
    font-size: 1vw;
}

.content-multas .content .button button:hover {
    background: rgb(44, 44, 44);
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}