.content-penal .content {
    margin-left: 2vw;
    margin-top: 2vw;
}

.content-penal .content header {
    display: flex;
    justify-content: space-between;
    width: 45vw;
}

.content-penal .content header h1 {
    font-size: 1.4vw;
    font-weight: 600;
}

.content-penal .content header button {
    border-color: #d80000;
    color: #fff;
    border-radius: 3vw;
    padding: .5vw 1vw;
    font-weight: 500;
    background-color: #d80000;
    font-family: "Poppins", sans-serif;
    font-size: .7vw;
    cursor: pointer;
    border-radius: 3vw;
    width: 10vw;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 4.5vh;
    border: 2px solid #d80000;
    margin-top: .8vw;
}

.content-penal .content header button:hover {
    filter: brightness(0.8);
}

.content-penal .content h1 {
    font-size: 1vw;
    font-weight: 600;
}

.content-tables {
    display: block;
    margin-left: -2vw;
    margin-top: 2vw;
} 

.modal-prender {
    margin-top: 3vw;
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.modal-prender .modal {
    width: 30vw;
    border-radius: .3rem;
    box-shadow: -1px 7px 18px -6px rgba(0, 0, 0, 0.4);
    background: rgba(255, 255, 255, .8);
}

.modal-prender .modal .content {
    margin-left: 2vw;
    margin-top: 1vw;
}

.modal-prender .modal h1 {
    font-size: 1.5rem;
    font-weight: 600;
}

.modal-prender .modal .inputs {
    display: flex;
    flex-direction: column;
    justify-content: left;
    width: 100%;
}

.modal-prender .modal .inputs label {
    color: #777;
    font-weight: 500;
    text-align: left;
    font-size: .8rem;
    margin-top: .5rem;
}

.modal-prender .modal .inputs input {
    background: transparent;
    border: 2px solid #777;
    border-radius: .5rem;
    padding: .5rem .8rem;
    width: 95%;
}

.selectParticipantes {
    width: 95%;
}

.modal-prender .modal .inputs input:last-child {
    margin-bottom: 1rem;
}

.modal-prender .modal .inputs input:focus {
    outline: none;
}

.modal-prender .modal button {
    width: 95%;
    border: 0;
    background: rgb(83, 202, 43);
    font-family: "Poppins";
    color: #f1f1f1;
    border-radius: .5rem;
    padding: .5rem .8rem;
    margin-bottom: 1rem;
    cursor: pointer;
}

.modal-prender .modal .prender-btn {
    background: #d80000;
}

.data-table-codigo-penal{
    margin-left: 1vw;
}