.content-funcionarios {
    width: 60vw;
    height: 70vh;
    display: flex;
}

.content-funcionarios .content {
    margin-left: 2vw;
    margin-top: 2vw;
}

.content-funcionarios .content header {
    width: 46vw;
    display: flex;
    align-items: center;
    justify-content: right;
    margin-bottom: 1vw;
    /* background-color: red; */
}

.content-funcionarios .content header h1 {
    color: #131313;
    font-weight: 600;
    font-size: 1.4vw;
    width: 50vw;
    /* background-color: green; */
}

.select {
    width: 95%;
}

.content-funcionarios .content header button {
    color: #131313;
    border-radius: 3vw;
    padding: .5vw 1vw;
    width: 10vw;
    font-weight: 500;
    background-color: transparent;
    font-family: "Poppins", sans-serif;
    font-size: .7vw;
    cursor: pointer;
    border-radius: 3vw;
    border: 2px solid #131313;
    background-color: purple;
}

.content-funcionarios .content header .contratar {
    color: rgb(255, 255, 255);
    border-radius: 3vw;
    padding: .5vw 1vw;
    font-weight: 500;
    background-color: rgb(83, 202, 43);
    font-family: "Poppins", sans-serif;
    margin-right: 2vw;
    font-size: .7vw;
    cursor: pointer;
    margin-left: 10vw;
    border-radius: 3vw;
    width: 10vw;
    border: 2px solid rgb(83, 202, 43);
}
.content-funcionarios .content header .contratar:hover {
    filter: brightness(0.85);
}

.content-funcionarios .content header .editar {
    color: rgb(255, 255, 255);
    border-radius: 3vw;
    padding: .5vw 1vw;
    font-weight: 500;
    background-color: rgb(43, 48, 202);
    font-family: "Poppins", sans-serif;
    margin-right: -8vw;
    font-size: .7vw;
    cursor: pointer;
    margin-left: 10vw;
    border-radius: 3vw;
    width: 10vw;
    border: 2px solid rgb(43, 48, 202);
}

.content-funcionarios .content header .editar:hover {
    filter: brightness(0.85);
}

.content-funcionarios .content header .exonerar {
    color: rgb(255, 255, 255);
    border-radius: 3vw;
    padding: .5vw 1vw;
    font-weight: 500;
    background-color: #d80000;
    font-family: "Poppins", sans-serif;
    font-size: .7vw;
    cursor: pointer;
    border-radius: 3vw;
    width: 10vw;
    border: 2px solid #d80000;
}

.content-funcionarios .content header .exonerar:hover {
    filter: brightness(0.85);
}

.content-funcionarios .content table {
    width: 44vw;
    table-layout: fixed;
    margin-top: 1vw;
}

.content-funcionarios .content .table-header {
    background-color: rgba(119, 119, 119, 0.323);
}

.content-funcionarios .content .table-content {
    height: 45vh;
    overflow-x: auto;
}


.content-funcionarios .content table {
    width: 44vw;
    table-layout: fixed;
}

.content-funcionarios .content .table-header {
    background-color: rgba(119, 119, 119, 0.323);
}

.content-funcionarios .content .table-content {
    height: 43vh;
    overflow-x: auto;
}

.content-funcionarios .content th {
    padding: 1vw;
    text-align: left;
    font-weight: bold;
    vertical-align: middle;
    font-size: .7vw;
    color: rgb(24, 23, 23);
    text-transform: uppercase;
}

.content-funcionarios .content td {
    text-align: left;
    padding-left: 1vw;
    vertical-align: middle;
    font-weight: 500;
    font-size: .7vw;
    color: rgb(26, 25, 25);
}

.content-funcionarios .content th:first-child {
    width: 4vw;
}

.content-funcionarios .content td:first-child {
    width: 4vw;
}

.content-funcionarios .content td button {
    background: none;
    padding: .3vw .5vw;
    border-radius: 3vw;
}

.modal-funcionarios {
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.modal-funcionarios .modal {
    width: 30vw;
    border-radius: .3rem;
    box-shadow: -1px 7px 18px -6px rgba(0, 0, 0, 0.4);
    background: rgba(255, 255, 255, .8);
}

.modal-funcionarios .modal .content {
    margin-left: 2vw;
    margin-top: 1vw;
}

.modal-funcionarios .modal h1 {
    font-size: 1.5rem;
    font-weight: 600;
}

.modal-funcionarios .modal .inputs {
    display: flex;
    flex-direction: column;
    justify-content: left;
    width: 100%;
}

.modal-funcionarios .modal .inputs label {
    color: #777;
    font-weight: 500;
    text-align: left;
    font-size: .8rem;
    margin-top: .5rem;
}

.modal-funcionarios .modal .inputs input {
    background: transparent;
    border: 2px solid #777;
    border-radius: .5rem;
    padding: .5rem .8rem;
    width: 95%;
}

.modal-funcionarios .modal .inputs input:last-child {
    margin-bottom: 1rem;
}

.modal-funcionarios .modal .inputs input:focus {
    outline: none;
}

.modal-funcionarios .modal button {
    width: 95%;
    border: 0;
    background: rgb(83, 202, 43);
    font-family: "Poppins";
    color: #f1f1f1;
    border-radius: .5rem;
    padding: .5rem .8rem;
    margin-bottom: 1rem;
    cursor: pointer;
}

.modal-funcionarios .modal button:hover {
    filter: brightness(0.7);
}

.modal-funcionarios .modal .exonerar-btn {
    background: #d80000;
}

.data-table-funcionarios{
    margin-left: -1vw;
}