.content-acoes {
    width: 50vw;
    height: 70vh;
    display: flex;
}

.content-acoes .content {
    margin-left: 2vw;
    margin-top: 2vw;
}

.content-acoes .content h1 {
    font-size: 1.4vw;
    font-weight: 600;
}

.content-acoes .content table {
    width: 45vw;
    table-layout: fixed;
}

.content-acoes .content .table-header {
    background-color: rgba(119, 119, 119, 0.323);
}

.content-acoes .content .table-content {
    height: 35vh;
    overflow-x: hidden;
}

.content-acoes .content th {
    padding: 1vw .8vw;
    text-align: left;
    font-weight: 600;
    font-size: .9vw;
    color: rgb(24, 23, 23);
    text-transform: uppercase;
}

.content-acoes .content td {
    padding: .2vw 1vw;
    text-align: left;
    vertical-align: middle;
    font-weight: 500;
    font-size: .8vw;
    color: rgb(26, 25, 25);
    width: 100%;
}

.content-acoes .content button {
    border: 1px solid #131313;
    background-color: transparent;
    display: inline-block;
    font-family: "Poppins", sans-serif;
    padding: .2vw .7vw;
    margin: .5vw;
    color: #d80000;
    border-color: #d80000;
    width: 5vw;
    font-size: .8vw;
}

.content-acoes .content td .participar {
    border-color: rgb(36, 170, 36);
    color: rgb(36, 170, 36);
}

.data-table-acoes{
    margin-left: -1vw;
}