.sidebar {
    background-color: #131313;
    width: 15vw;
    height: 70vh;
    border-top-left-radius: 1vw;
    border-bottom-left-radius: 1vw;
}

@media(max-width: 2560px){
    .sidebar{
        height: 90vh;
    }
}

.sidebar .top-sidebar img {
    width: 10vw;
    height: 10vw;
    display: flex;
    align-items: center;
    justify-content: center;
}

.sidebar .top-sidebar h1 {
    color: #f1f1f1;
    text-transform: uppercase;
    text-align: center;
    font-size: 1vw;
    font-weight: 500;
}

.sidebar .top-sidebar img {
    margin: 0 auto;
}

.sidebar-btn {
    width: 15vw;
    height: 5vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-top: 10vw;
}

.sidebar-btn a {
    background: transparent;
    border: 0;
    font-weight: 500;
    color: #f1f1f1bd;
    font-family: "Poppins";
    width: 15vw;
    height: 10vh;
    font-size: 1vw;
    text-align: left;
    margin-left: 1.5vw;
    margin-top: 1vw;
    text-decoration: none;
    /* border-left: 2px solid #f1f1f1bd; */
    padding-left: 1vw;
    display: flex;
    align-items: center;
    transition: .3s margin ease-in-out;
}

.sidebar-btn a:hover {
    margin-left: 5.5vw;
    color: #f1f1f1;
    border-color: #f1f1f1;
}

.sidebar-btn a:focus {
    margin-left: 5.5vw;
    color: #ffffff;
}