.content-procurar {
    width: 50vw;
    height: 70vh;
    display: flex;
    margin-left: 2vw;
    margin-top: 1vw;
}

.content-procurar .content h1 {
    font-size: 1.4vw;
    font-weight: 600;
}

.content-procurar .content .player-informations {
    width: 40vw;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 2vw;
}

.content-procurar .content .player-informations label {
    color: #131313aa;
    font-size: .8vw;
    margin-right: .5vw;
}

.content-procurar .content .player-informations h1 {
    font-size: #131313;
    font-size: 1vw;
    margin: 0;
    padding: 0;
    font-weight: 500;
    display: inline-block;
}

.content-procurar .content .player-informations .item {
    display: inline-block;
    margin-left: 5vw;
}

.content-procurar .content .player-informations .left {
    margin-left: -4.8vw;
}

.content-procurar .content .player-informations .right {
    width: 100%;
}

.content-procurar .content table {
    width: 44vw;
    table-layout: fixed;
}

.content-procurar .content .table-header {
    background-color: rgba(119, 119, 119, 0.323);
}

.content-procurar .content .table-content {
    height: 43vh;
    overflow-x: auto;
}

.content-procurar .content th {
    padding: .5vw .1vw;
    text-align: left;
    font-weight: bold;
    vertical-align: middle;
    font-size: .7vw;
    color: rgb(24, 23, 23);
    text-transform: uppercase;
}

.content-procurar .content td {
    padding: .5vw .1vw;
    text-align: left;
    vertical-align: middle;
    font-weight: 500;
    font-size: .7vw;
    color: rgb(26, 25, 25);
}

::-webkit-scrollbar {
    width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey; 
    border-radius: 5px;
}
   
/* Handle */
::-webkit-scrollbar-thumb {
    background: rgb(61, 59, 59); 
    border-radius: 5px;
}
  
/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #3a3a3a; 
}