.content-informacoes {
    width: 43vw;
    height: 65vh;
    display: flex;
    margin-left: 2vw;
    margin-top: 2vw;
}

.content-informacoes .content .opcoes {
    color: rgb(255, 255, 255);
    border-radius: 0.5vw;
    padding: .5vw 1vw;
    font-weight: 500;
    background-color: rgb(43, 48, 202);
    font-family: "Poppins", sans-serif;
    font-size: .7vw;
    cursor: pointer;
    width: 10vw;
    border: 2px solid rgb(43, 48, 202);
    margin-left: 1vw;
} 


.modal-informacoes {
    width: 100%;
    height: 60vw;
    display: flex;
    align-items: center;
    justify-content: center;
}

.modal-informacoes .modal {
    width: 50vw;
    border-radius: .3rem;
    box-shadow: -1px 7px 18px -6px rgba(0, 0, 0, 0.4);
    background: rgba(255, 255, 255, .8);
}

.modal-informacoes .modal .content {
    margin-left: 2vw;
    margin-top: 1vw;
}

.modal-informacoes .modal h1 {
    font-size: 1.5rem;
    font-weight: 600;
}

.modal-informacoes .modal .inputs {
    display: flex;
    flex-direction: column;
    justify-content: left;
    width: 100%;
}

.modal-informacoes .modal .inputs label {
    color: #777;
    font-weight: 500;
    text-align: left;
    font-size: .8rem;
    margin-top: .5rem;
}

.modal-informacoes .modal .inputs input {
    background: transparent;
    border: 2px solid #777;
    border-radius: .5rem;
    padding: .5rem .8rem;
    width: 95%;
}

.modal-informacoes .modal .inputs input:last-child {
    margin-bottom: 1rem;
}

.modal-informacoes .modal .inputs input:focus {
    outline: none;
}
