.content-dashboard {
    width: 50vw;
    height: 70vh;
    display: flex;
}

.content-dashboard .content .header-dashboard {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: 1vw;
    margin-top: 2vw;
}

.content-dashboard .content .header-dashboard h1 {
    width: 17vw;
    font-size: 1.4vw;
    font-weight: 600;
}

.content-dashboard .content .header-dashboard button {
    color: rgb(255, 255, 255);
    border-radius: 3vw;
    padding: .5vw 1vw;
    font-weight: 500;
    background-color: rgb(83, 202, 43);
    font-family: "Poppins", sans-serif;
    margin-right: 2vw;
    font-size: .7vw;
    cursor: pointer;
    margin-left: 6vw;
    border-radius: 3vw;
    width: 10vw;
    border: 2px solid rgb(83, 202, 43);
}

.content-dashboard .content .header-dashboard button:hover {
    filter: brightness(0.9);
}

.content-dashboard .content .header-dashboard button:focus {
    outline: none;
}

.content-dashboard .content .header-dashboard .vermelho {
    background-color: #d80000;
    border: 2px solid #d80000;
}

.content-dashboard .content .header-dashboard .vermelho:hover {
    background-color: #d80000;
    filter: brightness(0.9);
}

.content-dashboard .content .header-dashboard input {
    border: 2px solid #777;
    background-color: transparent;
    color: #777;
    border-radius: 3vw;
    padding: .5vw 1vw;
    width: 10vw;
    font-size: .7vw;
    text-align: center;
    font-weight: 500;
    font-family: "Poppins", sans-serif;
    margin-right: 2vw;
}

.content-dashboard .content .header-dashboard input:hover {
    filter: brightness(0.7);
}

.content-dashboard .content .header-dashboard input:focus {
    outline: none;
    border-color: #131313;
    color: #131313;
}

.content-dashboard .content .summary-content {
    display: flex;
    justify-content: space-around;
    margin-left: 1.5vw;
    margin-top: 2vw;
}

.content-dashboard .content .summary-content .summary {
    width: 11vw;
    height: 8vh;
    padding: .2vw;
    margin-left: .5vw;
}

.content-dashboard .content .summary-content .summary span {
    font-size: 1vw;
    font-weight: 500;
    color: #131313;
}

.content-dashboard .content .summary-content .summary h1 {
    font-size: 1.3vw;
    font-weight: 600;
    color: #131313;
    margin-top: -.2vw;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

.content-dashboard .content .graph {
    width: 45vw;
}