    .sidebar{
        height: 90vh;
    }
}

.sidebar .top-sidebar img {
    width: 10vw;
    height: 10vw;
    display: flex;
    align-items: center;
    justify-content: center;
}

.sidebar .top-sidebar h1 {
    color: #f1f1f1;
    text-transform: uppercase;
    text-align: center;
    font-size: 1vw;
    font-weight: 500;
}

.sidebar .top-sidebar img {
    margin: 0 auto;
}

.sidebar-btn {
    width: 15vw;
    height: 5vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-top: 10vw;
}

.sidebar-btn a {
    background: transparent;
    border: 0;
    font-weight: 500;
    color: #f1f1f1bd;
    font-family: "Poppins";
    width: 15vw;
    height: 10vh;
