    text-align: left;
    margin-left: 1.5vw;
    margin-top: 1vw;
    text-decoration: none;
    /* border-left: 2px solid #f1f1f1bd; */
    padding-left: 1vw;
    display: flex;
    align-items: center;
    transition: .3s margin ease-in-out;
}

.sidebar-btn a:hover {
    margin-left: 5.5vw;
    color: #f1f1f1;
    border-color: #f1f1f1;
}

.sidebar-btn a:focus {
    margin-left: 5.5vw;
