    box-shadow: -1px 7px 18px -6px rgba(0, 0, 0, 0.4);
    background: rgba(255, 255, 255, .8);
}

.modal-informacoes .modal .content {
    margin-left: 2vw;
    margin-top: 1vw;
}

.modal-informacoes .modal h1 {
    font-size: 1.5rem;
    font-weight: 600;
}

.modal-informacoes .modal .inputs {
    display: flex;
    flex-direction: column;
    justify-content: left;
    width: 100%;
}

.modal-informacoes .modal .inputs label {
    color: #777;
    font-weight: 500;
    text-align: left;
    font-size: .8rem;
    margin-top: .5rem;
}

.modal-informacoes .modal .inputs input {
    background: transparent;
    border: 2px solid #777;
    border-radius: .5rem;
    padding: .5rem .8rem;
    width: 95%;
}

.modal-informacoes .modal .inputs input:last-child {
    margin-bottom: 1rem;
}

.modal-informacoes .modal .inputs input:focus {
    outline: none;
}

