.container-arsenal {
    width: 50vw;
    height: 70vh;
    display: flex;
}

.container-arsenal .content {
    margin-left: 2vw;
    margin-top: 2vw;
}

 h1 {
    font-size: 1.4vw;
    font-weight: 600;
}

.allBox {
    display: flex;
    flex-wrap: wrap;
    text-align: center;
    width: 45vw;
    height: 40vw;
    max-width: 45vw;
    max-height: 45vw;
}

.loop {
    display: flex; 
    flex-wrap: wrap; 
    text-align: center; 
    width: 45vw;
    height: 40vw;
    max-width: 45vw;
    max-height: 45vw;
    overflow-y: scroll;
}

.contentBox {
    width: 9vw;
    height: 9vw;
    margin: 1vw;
    background: rgba(0, 0, 0, 0.933);
    border-radius: 1vw;
}

.contentBox .boxImg {
    width: 7vw;
    height: 5vw;
    margin-left: 1vw;
}

.contentBox .boxImg img {
    width: 7vw;
    height: 5vw;
}

.contentBox .boxName {
    width: 7vw;
    height: 1.4vw;
    font-family: "Poppins", sans-serif;
    color: #f1f1f1;
    margin-left: 1vw;
}

.contentBox .boxValue {
    position: relative;
    width: 7vw;
    height: 1.4vw;
    margin-top: -0.8vw;
    margin-left: 1vw;
    font-family: "Poppins", sans-serif;
    color: #f1f1f1;
}

.contentBox button {
    width: 70%;
    margin-top: -1.6vw;
    border: 0;
    background: rgba(0, 0, 0, 0.186);
    font-family: "Poppins";
    color: #f1f1f1;
    border-radius: .5rem;
    padding: .5rem .8rem;
    cursor: pointer;
}

.select-modal {
    width: 95%;
}